<template>
  <div class="preview_content">
    <div class="left">
      <header>摄像头列表</header>

      <ul>
        <li
          v-for="(item, index) in allCamera"
          :key="index"
          @click="handleClick(item.httpUrl, index)"
          :class="{ active: currentIndex == index }"
        >
          {{ item.cameraName }}
        </li>
      </ul>
    </div>
    <div class="right">
      <div class="mainContainer">
        <video id="videoElement" class="centeredVideo" controls>
          Your browser is too old which doesn't support HTML5 video.
        </video>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      allCamera: [],
      currentIndex: -1,
      currentUrl: "",
      flvPlayer: ""
    };
  },
  computed: {},
  created() {},
  mounted() {
    this.getAllCamera();
    this.palyVideo();
  },
  watch: {},
  methods: {
    getAllCamera() {
      this.$axios.post("/data/camera/findAll").then(res => {
        console.log("摄像头", res);
        let allCamera = res.data;
        allCamera.sort(this.$sortForArray("id"))
          this.allCamera = allCamera;

      });
    },
    handleClick(url, index) {
      this.currentIndex = index;
      this.currentUrl = url;
      if (this.flvPlayer != "") {
        console.log(this.flvPlayer);
        this.flvPlayer.unload();
        this.flvPlayer.detachMediaElement();
        this.flvPlayer.destroy();
        this.flvPlayer = '';
      }
      this.palyVideo();
    },
    palyVideo() {
      if (flvjs.isSupported() && this.currentUrl != "") {
        this.flvPlayer = flvjs.createPlayer({
          type: "flv",
          isLive: true,
          url: this.currentUrl,
          duration: 0,
          filesize: 0,
          enableStashBuffer: false,
          hasAudio: false,
          hasVideo: true
        });
        this.flvPlayer.attachMediaElement(videoElement);
        this.flvPlayer.load(); //加载
        this.flvPlayer.play();
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.preview_content {
  display: flex;
  height: 100%;
  .left {
    flex: 1;
    background-color: #ebebeb;

    header {
      margin-top: 20px;
      padding: 0 20px;
      font-size: 14px;
      display: flex;
      // justify-content: center;
      font-weight: 600;
    }

    ul {
      margin-top: 10px;
      padding-left: 10px;
      li {
        cursor: pointer;
        padding-left: 10px;
        font-size: 12px;
        height: 30px;
        line-height: 30px;
        color: #4d4c4c;
        &:hover {
          background-color: #e0e0e0;
        }
      }
      .active {
        background-color: #e0e0e0;
      }
    }
  }
  .right {
    flex: 6;

    // align-items: center;
    .mainContainer {
      padding: 20px;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      video {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>



